import React from 'react'

// objects 
const firstProduct = {
    name: 'CREED AVENTUS EAU DE PARFUM',
    img: '/img/CREED AVENTUS EAU DE PARFUM.jpg',
    description: 'A sensual, audacious and contemporary scent',
    price: 'USD$24.99'
}

const secondProduct = {
    name: 'DIESEL ONLY THE BRAVE TATTOO',
    img: '/img/DIESEL ONLY THE BRAVE TATTOO.jpg',
    description: 'A strikingly fruity scent for a great price, perfect for young dudes',
    price: 'USD$25.99'
}

const Products = () => {
  return (
      <div className='products'>
          <h1>Props method 4: destructuring directly in  function parameters</h1>
          <Product 
            name={firstProduct.name}
            img={firstProduct.img}
            description={firstProduct.description}
            price={firstProduct.price}
          />
          <Product 
            name={secondProduct.name}
            img= {secondProduct.img}
            description= {secondProduct.description}
            price= {secondProduct.price}
          />
          <h4>Gives the same result</h4>
      </div>
  )
}

const Product = ({name, img, description, price}) => {
    return (
        <div className='product'>
            <img src={img} alt="im"/>
            <h1>{name}</h1>
            <h4>{description}</h4>
            <p>{price}</p>
        </div>
    )
}

function Prop4 () {
  return (
    <div>
      <Products />
    </div>
  )
}

export default Prop4