import React from 'react'

const Products = () => {
  return (
      <div className='products'>
          <h1>Using Props Method 1: Hard coding into the component </h1>
          <Product 
            name='CREED AVENTUS EAU DE PARFUM'
            img='/img/CREED AVENTUS EAU DE PARFUM.jpg'
            description='A sensual, audacious and contemporary scent'
            price='US$24.99'
          />
          <Product 
            name='DIESEL ONLY THE BRAVE TATTOO'
            img='/img/DIESEL ONLY THE BRAVE TATTOO.jpg'
            description='A strikingly fruity scent for a great price, perfect for young dudes'
            price='US$58.99'
          />
          <Product
            name='JO MALONE LONDON HUNTSMAN'
            img='/img/JO MALONE LONDON HUNTSMAN.jpg'
            description='A grown-up scent with multiple versions'
            price='USD $124.99' 
          />
      </div>
  )
}

const Product = (props) => {
    return (
        <div className='product'>
            <img src={props.img} alt="im"/>
            <h1>{props.name}</h1>
            <h4>{props.description}</h4>
            <p>{props.price}</p>
        </div>
    )
}

function Prop1 () {
  return (
    <div>
      <Products />
    </div>
  )
}

export default Prop1