import React from 'react'

// array
const pef = [
    {
        id: 1,
        name: 'CREED AVENTUS EAU DE PARFUM',
        img: '/img/CREED AVENTUS EAU DE PARFUM.jpg',
        description: 'A sensual, audacious and contemporary scent',
        price: 'USD$24.99'
    },
    {
        id: 2,
        name: 'DIESEL ONLY THE BRAVE TATTOO',
        img: '/img/DIESEL ONLY THE BRAVE TATTOO.jpg',
        description: 'A strikingly fruity scent for a great price, perfect for young dudes',
        price: 'USD$25.99'
    }
]

const Products = () => {
    let newPef = pef.map((each) => {
        // spread operator
        return <Product key={each.id} {...each} />
    })

    return (
        <div className='products'>
            <h1>Events</h1>
            {newPef}
            <h4>Gives the same result</h4>
        </div>
    )
}

const Product = (props) => {
    const { name, img, description, price } = props

    const showDescription = () => {
        console.log(description)
    }

    const showName = () => {
        console.log(name)
    }

    return (
        <div className='product'>
            <img src={img} alt="im" />
            <h1>{name}</h1>
            <h4>{description}</h4>
            <p>{price}</p>
            {/* simple event  */}
            <button type='button' onClick={showDescription} >Click to console Description</button>
            {/* if/when you have to pass an arguement into an event, set up an arrow function so it does not lauch immmediately you reload the page. the button below is just an example of when you pass an arguement, without the arrow function in front of it, the event will lauch immediately you reload the page */}
            <button type='button' onClick={() => showName(name)} >Click to console name</button>
            {/* inline event  */}
            <button type='button' onClick={() => console.log(price)} >Click to console price</button>
        </div>
    )
}

function Event() {
    return (
        <div>
            <Products />
        </div>
    )
}

export default Event