import React from 'react'

const Title = () => <p>Basic React Rendering</p>
const Products = () => {
  return (
      <div className='products'>
          <h1><Title /></h1>
          <Product />
          <Product />
          <Product />
      </div>
  )
}

const Product = () => {
    return (
        <div className='product'>
            <img src='/img/DIESEL ONLY THE BRAVE TATTOO.jpg' alt="im"/>
            <h1>DIESEL ONLY THE BRAVE TATTOO</h1>
            <h4>A strikingly fruity scent for a great price, perfect for young dudes</h4>
            <p>US$58.99</p>
        </div>
    )
}

function Render () {
  return (
    <div>
      <Products />
    </div>
  )
}

export default Render