import React from 'react'

// array
const pef = [
  {
    id: 1,
    name: 'CREED AVENTUS EAU DE PARFUM',
    img: '/img/CREED AVENTUS EAU DE PARFUM.jpg',
    description: 'A sensual, audacious and contemporary scent',
    price: 'USD$24.99'
  },
  {
    id: 2,
    name: 'DIESEL ONLY THE BRAVE TATTOO',
    img: '/img/DIESEL ONLY THE BRAVE TATTOO.jpg',
    description: 'A strikingly fruity scent for a great price, perfect for young dudes',
    price: 'USD$25.99'
  }
]

const Product = (props) => {
  const { name, img, description, price } = props
  return (
    <div className='product'>
      <img src={img} alt="im" />
      <h1>{name}</h1>
      <h4>{description}</h4>
      <p>{price}</p>
    </div>
  )
}

const Products = () => {
  let newPef = pef.map((each) => {
    const { id, name, img, description, price } = each
    return <Product
      key={id}
      name={name}
      img={img}
      description={description}
      price={price}
    />
  })

  return (
    <div className='products'>
      <h1>iterating over an array using the map method 1</h1>
      {newPef}
      <h4>Gives the same result</h4>
    </div>
  )
}

